'use client'

import * as React from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import {
  Home,
  FileText,
  Settings,
  Menu,
  ChevronLeft,
  ChevronRight,
} from "lucide-react"

import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet"
import { Button } from "@/components/ui/button"

const navItems = [
  { name: "Dashboard", href: "/dashboard", icon: Home },
  { name: "Receipts", href: "/receipts", icon: FileText },
  { name: "Settings", href: "/settings", icon: Settings },
]

export function Sidebar() {
  const pathname = usePathname()
  const [collapsed, setCollapsed] = React.useState(false)
  const [open, setOpen] = React.useState(false)

  const NavLinks = () => (
    <nav className="flex flex-col space-y-1 mt-4">
      {navItems.map((item) => {
        const Icon = item.icon
        const active = pathname.startsWith(item.href)
        return (
          <Link
            key={item.href}
            href={item.href}
            onClick={() => setOpen(false)}
            className={cn(
              "flex items-center gap-3 rounded-lg px-3 py-2 text-sm font-medium transition",
              active
                ? "bg-muted text-primary"
                : "text-muted-foreground hover:bg-muted/50"
            )}
          >
            <Icon className="h-5 w-5" />
            {!collapsed && <span>{item.name}</span>}
          </Link>
        )
      })}
    </nav>
  )

  return (
    <>
      {/* Desktop Sidebar */}
      <aside
        className={cn(
          "hidden md:flex flex-col h-screen border-r bg-background transition-all duration-300",
          collapsed ? "w-20" : "w-64"
        )}
      >
        <div className="flex items-center justify-between px-4 py-4 border-b">
          {!collapsed && <span className="font-bold text-lg">e-Receipt</span>}
          <Button
            variant="ghost"
            size="icon"
            onClick={() => setCollapsed(!collapsed)}
          >
            {collapsed ? <ChevronRight className="h-5 w-5" /> : <ChevronLeft className="h-5 w-5" />}
          </Button>
        </div>
        <div className="flex-1 overflow-y-auto p-3">
          <NavLinks />
        </div>
      </aside>

      {/* Mobile Floating Button + Drawer */}
      <div className="md:hidden fixed bottom-5 left-5 z-50">
        <Sheet open={open} onOpenChange={setOpen}>
          <SheetTrigger asChild>
            <Button
              variant="default"
              size="icon"
              className="rounded-full shadow-lg bg-white"
            >
              <Menu className="h-6 w-6 text-gray-700" />
            </Button>
          </SheetTrigger>

          <SheetContent side="left" className="w-64 p-4">
            <div className="flex items-center justify-between mb-4">
              <span className="font-bold text-lg">e-Receipt</span>
              <Button variant="ghost" size="icon" onClick={() => setOpen(false)}>
                <ChevronLeft className="h-5 w-5" />
              </Button>
            </div>
            <NavLinks />
          </SheetContent>
        </Sheet>
      </div>
    </>
  )
}
